<template>
    <v-container>
        <v-row>
            <v-col cols="12">
                <h2>Crea tu cuenta</h2>
            </v-col>

            <v-col cols="12">
                <v-card>
                    <v-progress-linear
                            value="50"
                            height="10"
                            color="blue-grey"
                            reactive
                            dark
                    ></v-progress-linear>
                    <v-container>
                        <v-form v-model="validForm">
                            <v-text-field
                                    v-model="name"
                                    :rules="[rules.required]"
                                    label="Nombre"
                                    required
                            ></v-text-field>

                            <v-text-field
                                    v-model="lastName"
                                    :rules="[rules.required]"
                                    label="Apellidos"
                                    required
                            ></v-text-field>


                            <v-text-field
                                    v-model="email"
                                    :rules="[rules.required, rules.email]"
                                    label="E-mail"
                                    required
                            ></v-text-field>

                            <v-text-field
                                    v-model="password"
                                    :rules="[rules.required, rules.min8Char, rules.samePassword]"
                                    :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                                    :type="showPassword ? 'text' : 'password'"
                                    label="Contraseña"
                                    hint="Mínimo 8 caracteres"
                                    counter
                                    @click:append="showPassword = !showPassword"
                                    required
                            ></v-text-field>

                            <v-text-field
                                    v-model="password2"
                                    :rules="[rules.required, rules.min8Char, rules.samePassword]"
                                    :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                                    :type="showPassword ? 'text' : 'password'"
                                    label="Repite la contraseña"
                                    hint="Mínimo 8 caracteres"
                                    counter
                                    @click:append="showPassword = !showPassword"
                                    required
                            ></v-text-field>
                        </v-form>

                        <v-btn block color="primary" @click="signUp" :disabled="buttonIsDisabled">Registrar</v-btn>
                    </v-container>
                </v-card>
            </v-col>

            <v-col cols="12" align="center" id="login-now-claim">
                <p>¿Ya tienes una cuenta? <router-link to="/login">¡Accede ahora!</router-link></p>
            </v-col>
        </v-row>

        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
        >
            {{ snackbarText }}
            <v-btn
                    color="blue"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import { mapActions } from 'vuex';
    import Constants from "../store/Constants";

    export default {
        name: "signUp",
        data(){
            return {
                validForm: false,
                name: '',
                lastName: '',

                email:'',
                password:'',
                password2:'',
                showPassword: false,
                buttonIsDisabled: true,
                snackbar: false,
                snackbarText:'',
                rules: {
                    required: value => !!value || Constants.RULE_TEXT_MANDATORY_FIELD,

                    min8Char: value => (value.length >= 8 || value.length == 0) || 'Mínimo 8 caracteres',
                    email: value => {
                        const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
                        return pattern.test(value) || 'E-mail no válido'
                    },

                    samePassword: value => value.localeCompare(this.password) == 0 || 'Las contraseñas no coinciden',
                },
                closeText: Constants.TEXT_CLOSE,
            }
        },
        methods:{
            ...mapActions([
                'isUsernameInUseFirebase',
                'registerUserFirebase',
            ]),
            signUp: function () {

                //Create firebase user
                let resultRegisterUserFirebasethis = this.registerUserFirebase(
                    {name: this.name,
                        lastName: this.lastName,
                        email: this.email,
                    password: this.password});

                resultRegisterUserFirebasethis.then(() => {
                        console.log('Successfully updated user');
                        this.$router.push('email-verification');
                    })
                    .catch((error) => {
                        this.snackbarText = error;
                        this.snackbar = true;
                    });
            },

        },
        watch: {
            validForm: function (value) {
                if(value){
                    this.buttonIsDisabled = false
                }
                else{
                    this.buttonIsDisabled = true
                }
            }
        }
    }
</script>

<style scoped>

</style>
