<template>
    <v-container style="height: 100%; position: relative; width: 100%" align="center">
        <v-row>
            <v-col cols="12">
                <h2>Elige tu foto de perfil</h2>
            </v-col>
        </v-row>

        <v-row style="height: 100%">
            <v-col cols="12"
                   sm="6" offset-sm="3"
                   md="4" offset-md="4"
                   style="height: 100%">
                <v-card
                        class="mx-auto"
                        style="height: 75%"
                        align="center"
                >
                    <v-progress-linear
                            value="100"
                            height="10px"
                            color="blue-grey"
                            dark
                    >
                    </v-progress-linear>

                    <v-avatar class="thumbnail" style="width: 95%; padding-top: 95%; vertical-align: top; position: relative; margin-top: 5%">
                            <img
                            v-if="showImg"
                            style="width: 100%; position: absolute; top: 0; height: 100%; object-position: center; object-fit: cover;"
                            class="white--text align-end"
                            :aspect-ratio="1"
                            :src="imagePreview"
                            >
                    </v-avatar>

                    <v-btn
                            absolute
                            dark
                            fab
                            right
                            color="primary"
                            @click="$refs.myFiles.click()"
                            class="button-open-camera"
                    >
                        <v-icon>mdi-camera</v-icon>
                    </v-btn>

                    <v-container class="bottom-centered-buttons">
                        <v-btn block color="primary" @click="uploadUserProfileImage" :disabled="buttonIsDisabled">Continuar</v-btn>
                    </v-container>
                </v-card>
            </v-col>
        </v-row>

        <input hidden ref="myFiles" @change="previewFiles" type="file" accept="image/*" capture>
        <!--SnackBar-->
        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
        >
            {{ snackbarText }}
            <v-btn
                    color="blue"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
        <!--End SnackBar-->
    </v-container>

</template>

<script>
    import Vue from 'vue'
    import imageCompression from 'browser-image-compression'
    import Constants from "../store/Constants";
    import { mapActions } from 'vuex';

    export default {
        name: "SetYourProfileImage",
        data() {
            return {
                showImg: true,
                imagePreview: this.$store.getters.getDefaultInfoUserImageByGender,
                buttonIsDisabled: false,
                imageUpload: null,
                snackbar: false,
                snackbarText: '',
                closeText: Constants.TEXT_CLOSE
            }
        },
        methods: {
            ...mapActions([
                'setUserProfileImageDatabase',
            ]),
            previewFiles() {
                if (this.$refs.myFiles.files.length > 0) {
                    if (!this.$refs.myFiles.files[0].type.includes('image/')) {
                        this.snackbarText = Constants.ASK_FOR_IMAGE_SELECTION;
                        this.snackbar = true;
                        return
                    }

                    let imageFile = this.$refs.myFiles.files[0];

                    let options = {
                        maxSizeMB: 0.5,
                        maxWidthOrHeight: 500,
                        useWebWorker: true
                    };

                    imageCompression(imageFile, options)
                            .then((compressedFile) => {
                            if (typeof FileReader === 'function') {
                                const reader = new FileReader();
                                reader.onload = (event) => {
                                    this.imgSrc = event.target.result;
                                    this.imagePreview = this.imgSrc;
                                    this.imageUpload = this.imagePreview.replace("data:image/jpeg;base64,", "").replace("data:image/png;base64,", "");
                                    console.log(this.imagePreview);
                                    this.showImg = false;
                                    Vue.nextTick(() => {
                                        this.showImg = true;
                                    })
                                };
                                reader.readAsDataURL(compressedFile)
                            } else {
                                this.snackbarText = Constants.ERROR_API_FILEREADER_NOT_SUPPORTED;
                                this.snackbar = true;
                            }
                        })
                        .catch(function (error) {
                            console.log(error.message);
                        });
                } else {
                    console.warn("img error")
                }
            },
            uploadUserProfileImage() {
                let self = this;

                let resultSetUserProfileImageDatabase = this.setUserProfileImageDatabase(self.imageUpload);

                resultSetUserProfileImageDatabase.then(() => {
                                        this.$router.replace('dashboard');
                                    })
                                    .catch(function (error) {
                                        self.snackbarText = error;
                                        self.snackbar = true;
                                        return
                                    });
            },
        },
    }
</script>

<style scoped>
    .button-open-camera {
        margin-top: 85%;
    }

    .bottom-centered-buttons{
        width: 92.5%;
        bottom:3%;
        left: 50%;
        transform: translate(-50%, 0);
        position: absolute;
    }
</style>