<template>
    <v-app>
        <div v-if="dataReady">
                <router-view style="width: 100%;"/>
        </div>
        <div id="loading" v-if="!dataReady">
            <v-progress-circular
                    :size="70"
                    :width="7"
                    color="primary"
                    indeterminate
            ></v-progress-circular>
        </div>
    </v-app>
</template>

<script>

    import {mapActions} from 'vuex';
    import firebase from "firebase";

    export default {
        name: 'App',
        components: {},
        data: () => ({
            'dataReady': false,
        }),
        methods: {
            ...mapActions([
                'getAppInfoForLoggedUserFirebase',
            ]),
            getAppInfoForLoggedUser: function () {
                let self = this;
                let resultGetAppInfoForLoggedUserFirebasethis = self.getAppInfoForLoggedUserFirebase();

                resultGetAppInfoForLoggedUserFirebasethis.then(function () {
                    self.dataReady = true;
                })
                    .catch((error) => {
                        console.error(error)
                    });

                return resultGetAppInfoForLoggedUserFirebasethis;
            }
        },
        created() {
            let self = this;
            firebase.auth().onAuthStateChanged(function(currentUser) {
                if (currentUser) {
                    self.getAppInfoForLoggedUser();
                } else {
                    self.dataReady = true;
                }
            });
        }
    };
</script>

<style>
    #app-toolbar {
        width: 100%;
    }
    #loading {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        min-height: 100vh;
    }
</style>
