<template>

    <v-container class="email-verification">
        <v-row>
            <v-col cols="12">
                <h2>Verifica tu E-mail</h2>
            </v-col>
        </v-row>

        <v-col cols="12">
            <v-card>
                <v-progress-linear
                        value="100"
                        height="10"
                        color="blue-grey"
                        reactive
                        dark
                ></v-progress-linear>

                <v-container>
                    <v-row>
                        <v-col align="center">
                            <br>
                            <br>
                            <v-progress-circular
                                    size="100"
                                    color="primary"
                                    indeterminate
                            ></v-progress-circular>
                            <br>
                            <br>
                        </v-col>
                    </v-row>

                    <v-row>
                        <v-col>
                            Para poder continuar, completa el proceso de verificación a través del enlace que hemos enviado a tu E-mail.
                        </v-col>
                    </v-row>
                </v-container>
            </v-card>
        </v-col>

        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
        >
            {{ snackbarText }}
            <v-btn
                    color="blue"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import { mapActions } from 'vuex';
    import Constants from "../store/Constants";

    export default {
        name: "emailVerification",
        data(){
            return {
                sendEmailButtonIsDisabled: true,
                snackbar: false,
                snackbarText:'',
                isEmailVerified: false,
                intervalCheckEmailVerified: null,
                user: null,
                closeText: Constants.TEXT_CLOSE
            }
        },
        methods:{
            ...mapActions([
                'sendEmailVerificationFirebase',
                'checkEmailVerifiedFirebase',
            ]),
            sendEmailVerification: function (){
                //Send verification email
                let resultSendEmailVerificationFirebase = this.sendEmailVerificationFirebase();

                resultSendEmailVerificationFirebase
                    .catch((error) => {
                        this.snackbarText = error;
                        this.snackbar = true;
                    });
            },
            checkEmailVerified: function () {
                let resultCheckEmailVerifiedFirebase = this.checkEmailVerifiedFirebase();

                resultCheckEmailVerifiedFirebase
                    .then((isVerified) => {
                        if(isVerified){
                            clearInterval(this.intervalCheckEmailVerified);
                            this.$router.replace('fill-your-profile-info');
                        }
                    })
                    .catch((error) => {
                        this.snackbarText = error;
                        this.snackbar = true;
                    });

            }
        },
        beforeMount(){
            this.sendEmailVerification();
            this.intervalCheckEmailVerified = setInterval(this.checkEmailVerified,2000);
        },
    }
</script>

<style scoped>
</style>
