<template>
    <v-container>
        <h4>{{knownLanguagesTitle}}</h4>
        <v-combobox
                v-model="chips_known_languages"
                :items="items_known_languages"
                chips
                type="button"
                clearable
                :label="knownLanguagesLabel"
                multiple
                solo
        >
            <template v-slot:item="{ index, item }">
                <v-chip
                        color="white"
                        block
                >
                    <v-avatar left>
                        <v-img :src="createPathToFlagImage(item.id)"></v-img>
                    </v-avatar>
                    <strong>{{ item.text }}</strong>&nbsp;
                </v-chip>
            </template>
            <!--<template v-slot:item="{ index, item }">
                <v-chip
                        block
                >
                    <v-avatar left>
                        <v-img :src = "createPathToFlagImage(item.id)"></v-img>
                    </v-avatar>
                    <strong>{{ item.text }}</strong>&nbsp;
                </v-chip>
            </template>-->

            <template v-slot:selection="{ attrs, item, select, selected }">
                <v-chip
                        v-bind="attrs"
                        :input-value="selected"
                        close
                        color="primary"
                        @click="select"
                        @click:close="remove_known_language(item)"
                >
                    <v-avatar left>
                        <v-img :src = "createPathToFlagImage(item.id)"></v-img>
                    </v-avatar>
                    <strong>{{ item.text }}</strong>&nbsp;
                </v-chip>
            </template>
        </v-combobox>

        <h4>{{languagesToLearnTitle}}</h4>
        <v-combobox
                v-model="chips_languages_to_learn"
                :items="items_languages_to_learn"
                chips
                clearable
                type="button"
                :label="languagesToLearnLabel"
                multiple
                solo
        >
            <template v-slot:item="{ index, item }">
                <v-chip
                        color="white"
                        block
                >
                    <v-avatar left>
                        <v-img :src="createPathToFlagImage(item.id)"></v-img>
                    </v-avatar>
                    <strong>{{ item.text }}</strong>&nbsp;
                </v-chip>
            </template>
            <!--<template v-slot:item="{ index, item }">
                <v-chip
                        color="white"
                        block
                >
                    <v-avatar left>
                        <v-img :src="createPathToFlagImage(item.id)"></v-img>
                    </v-avatar>
                    <strong>{{ item.text }}</strong>&nbsp;
                </v-chip>
            </template>-->
            <template v-slot:selection="{ attrs, item, select, selected }">
                <v-chip
                        v-bind="attrs"
                        :input-value="selected"
                        close
                        color="primary"
                        @click="select"
                        @click:close="remove_language_to_learn(item)"
                >
                    <v-avatar left>
                        <v-img :src = "createPathToFlagImage(item.id)"></v-img>
                    </v-avatar>
                    <strong>{{ item.text }}</strong>&nbsp;
                </v-chip>
            </template>
        </v-combobox>

        <v-btn block color="primary" @click="setLanguages" :disabled="buttonIsDisabled">Continuar</v-btn>

        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
        >
            {{ snackbarText }}
            <v-btn
                    color="blue"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import { mapActions } from 'vuex';
    import Constants from "../../store/Constants";

    export default {
        name: "YourLanguagesSelector",
        data () {
            return {
                buttonIsDisabled: true,
                snackbar: false,
                snackbarText:'',
                chips_known_languages: [],
                chips_languages_to_learn: [],
                items_known_languages: this.$store.getters.getAllAvailableLanguagesAsArray.filter(function( lang ) {
                    return lang.id != "all";
                }),
                items_languages_to_learn: this.$store.getters.getAllAvailableLanguagesAsArray.filter(function( lang ) {
                    return lang.id != "all";
                }),
                languagesToLearnLabel: Constants.TEXT_LANGUAGES_YOU_WANT_TO_IMPROVE,
                languagesToLearnTitle: Constants.TEXT_LANGUAGES_YOU_WANT_TO_PRACTICE,
                knownLanguagesLabel: Constants.TEXT_LANGUAGES_YOU_SPEAK_FLUENTLY,
                knownLanguagesTitle: Constants.TEXT_MOTHER_LANGUAGES,
                closeText: Constants.TEXT_CLOSE
            }
        },
        methods: {
            ...mapActions([
                'setUserLanguagesFirebase',
            ]),
            remove_known_language (item) {
                this.chips_known_languages.splice(this.chips_known_languages.indexOf(item), 1)
                this.chips_known_languages = [...this.chips_known_languages]
            },
            remove_language_to_learn (item) {
                this.chips_languages_to_learn.splice(this.chips_languages_to_learn.indexOf(item), 1)
                this.chips_languages_to_learn = [...this.chips_languages_to_learn]
            },
            validLanguages() {
                if (this.chips_known_languages.length > 0 && this.chips_languages_to_learn.length > 0) {
                    this.buttonIsDisabled = false;
                } else {
                    this.buttonIsDisabled = true;
                }
            },
            filter_items_known_languages: function(){
                // eslint-disable-next-line no-unused-vars
                let self = this;
                this.items_known_languages = this.$store.getters.getAllAvailableLanguagesAsArray.filter(function( lang ) {
                    return lang.id != "all";
                }).filter(function (value) {
                    return !self.chips_languages_to_learn.includes(value);
                })
            },
            filter_items_languages_to_learn: function(){
                // eslint-disable-next-line no-unused-vars
                let self = this;
                this.items_languages_to_learn = this.$store.getters.getAllAvailableLanguagesAsArray.filter(function( lang ) {
                    return lang.id != "all";
                }).filter(function (value) {
                    return !self.chips_known_languages.includes(value);
                })
            },
            setLanguages(){
                //Set languages to user profile
                let resultSetUserLanguagesFirebase = this.setUserLanguagesFirebase(
                    {knownLanguages: this.chips_known_languages,
                    languagesToLearn: this.chips_languages_to_learn});

                resultSetUserLanguagesFirebase.then(() => {
                    this.$router.replace('set-your-profile-image');
                })
                    .catch((error) => {
                        this.snackbarText = error;
                        this.snackbar = true;
                    });
            },
            createPathToFlagImage: function (lang) {
                return this.$store.getters.getPathToFlagImageByLanguageID(lang);
            }
        },
        watch: {
            chips_known_languages: function(){
                this.validLanguages();
                this.filter_items_languages_to_learn();
            } ,
            chips_languages_to_learn: function(){
                this.validLanguages();
                this.filter_items_known_languages();
            },
        },
    }
</script>

