<template>
    <v-container>
        <v-row>
            <v-col cols="12">
                <h2>Rellena tu perfil</h2>
            </v-col>
            <v-col cols="12">
                <v-card>
                    <v-progress-linear
                            value="25"
                            height="10"
                            color="blue-grey"
                            reactive
                            dark
                    >
                    </v-progress-linear>

                    <v-container>
                        <v-form v-model="validForm">
                            <v-text-field
                                    v-model="username"
                                    :rules="[rules.required, rules.min4Char, rules.validUsername]"
                                    label="Nombre de usuario"
                                    counter
                                    maxlength="10"
                                    required
                            ></v-text-field>

                            <v-dialog
                                    ref="dialog"
                                    v-model="modal"
                                    :return-value.sync="birthDate"
                                    persistent
                            >
                                <template v-slot:activator="{ on }">
                                    <v-text-field
                                            v-model="birthDateInTextMonthFormat"
                                            label="Fecha de nacimiento"
                                            append-icon="mdi-calendar"
                                            :rules="[rules.required, rules.minAge]"
                                            readonly
                                            v-on="on"
                                            required
                                    ></v-text-field>
                                </template>
                                <v-date-picker v-model="birthDate" scrollable width="97%" locale="es">
                                    <v-spacer></v-spacer>
                                    <v-btn text color="primary" @click="modal = false">Cancel</v-btn>
                                    <v-btn text color="primary" @click="$refs.dialog.save(birthDate)">OK</v-btn>
                                </v-date-picker>
                            </v-dialog>

                            <v-select
                                    v-model="gender"
                                    :rules="[rules.required]"
                                    style="margin-top: 15px"
                                    :items="genderOptions"
                                    item-value="id"
                                    item-text="option"
                                    label="Género"
                                    dense
                                    return-object
                            >

                                <template v-slot:item="{item}">
                                    <v-avatar>
                                        <img :src="item.img" style="height: 60%; width: auto"/>
                                    </v-avatar>
                                    {{    item.name }}
                                </template>
                                <template v-slot:selection="{item, select, selected }">
                                    <v-avatar>
                                        <img :src="item.img" style="height: 50%; width: auto"/>
                                    </v-avatar>
                                    {{  item.name }}
                                </template>
                            </v-select>

                            <v-textarea
                                    style="margin-top: 15px"
                                    outlined
                                    v-model="description"
                                    :rules="[rules.required, rules.min20Char]"
                                    label="Cuéntanos un poco sobre ti"
                                    required
                                    counter
                                    maxlength="140"
                            ></v-textarea>
                        </v-form>

                        <v-btn block color="primary" @click="setProfileInfo" :disabled="buttonIsDisabled">Continuar</v-btn>
                    </v-container>
                </v-card>
            </v-col>

            <!-- Error snackbar -->
            <v-snackbar
                    v-model="snackbar"
                    :timeout="4000"
            >
                {{ snackbarText }}
                <v-btn
                        color="blue"
                        text
                        @click="snackbar = false"
                >
                    {{closeText}}
            </v-btn>
        </v-snackbar>
            <!--End snackbar-->
        </v-row>
    </v-container>
</template>

<script>
    import { mapActions } from 'vuex';
    import Constants from "../store/Constants";
    const minimumAge = 16;
    
    export default {
        name: "fillYourProfileInfo",
        data() {
            return {
                buttonIsDisabled: true,
                snackbar: false,
                snackbarText: '',
                username:'',
                birthDate: null,
                birthDateDateFormat: null,
                firstTimeOpenCalendar: true,
                menu: false,
                modal: false,
                description: '',
                gender: null,
                validForm: false,
                genderOptions: this.$store.getters.getAllAvailableGendersAsArray,
                rules: {
                    required: value => !!value || 'Este campo es obligatorio',
                    min4Char: value => (value.length >= Constants.RULE_MIN_VALUE_CHARACTERS_USERNAME || value.length == 0) || Constants.RULE_TEXT_MIN_VALUE_CHARACTERS_USERNAME,
                    min20Char: value => (value.length >= Constants.RULE_MIN_VALUE_CHARACTERS_USER_DESCRIPTION || value.length == 0) || Constants.RULE_TEXT_MIN_VALUE_CHARACTERS_USER_DESCRIPTION,
                    minAge: value => {
                        const bDate = new Date(value);
                        const currentDate = new Date();
                        currentDate.setHours(0,0,0);
                        let validAge;
                        if (currentDate.getFullYear() - bDate.getFullYear() < minimumAge) {
                            validAge = false;
                        } else if (currentDate.getFullYear() - bDate.getFullYear() == minimumAge) {
                            if (currentDate.getMonth() < bDate.getMonth()) {
                                validAge = false;
                            } else if (currentDate.getMonth() == bDate.getMonth()) {
                                if (currentDate.getDate() < bDate.getDate()) {
                                    validAge = false;
                                } else {
                                    validAge = true;
                                }
                            } else {
                                validAge = true;
                            }
                        } else {
                            validAge = true;
                        }

                        return validAge || 'Debes tener al menos' + this.minimumAge + ' años para poder registrarte';
                    },
                    validUsername: value => {
                        const pattern1 = /[a-zA-Z_]+[a-zA-Z0-9_]*/
                        const pattern2 = /^_/
                        const pattern3 = /_[_]+/
                        return (pattern1.test(value) && !pattern2.test(value) && !pattern3.test(value)) || 'Nombre de usuario no válido'
                    },
                },
                closeText: Constants.TEXT_CLOSE
            }
        },
        methods: {
            ...mapActions([
                'setUserProfileInfoFirebase',
                'isUsernameInUseFirebase'
            ]),
            setProfileInfo: async function () {
                // Is username registered?
                try {
                    if ( await this.isUsernameInUseFirebase(this.username)) {
                        this.snackbarText = Constants.ERROR_ALREADY_AN_ACCOUNT_WITH_THIS_USERNAME;
                        this.snackbar = true;
                        return;
                    }
                }
                catch (error) {
                    this.snackbarText = error;
                    this.snackbar = true;
                    return;
                }

                let resultSetUserProfileInfoFirebase = this.setUserProfileInfoFirebase({
                    username: this.username,
                    birthDate: (new Date(this.birthDate)).setHours(0,0,0,),
                    gender: this.gender.id,
                    description: this.description
                });

                resultSetUserProfileInfoFirebase
                    .then(() => {
                        this.$router.push('fill-your-languages');
                    })
                    .catch((error) => {
                        this.snackbarText = error;
                        this.snackbar = true;
                    });
            },
            setDefaultBirthDate: function(){
                if(this.firstTimeOpenCalendar){
                    let currentDate = new Date();
                    currentDate.setFullYear(currentDate.getFullYear()-minimumAge);
                    this.birthDate = currentDate.toISOString().substr(0, 10)
                    this.firstTimeOpenCalendar = false;
                }
            }
        },
        computed: {
            birthDateInTextMonthFormat: function () {
                return this.$store.getters.getFormattedDateInTextMonth(this.birthDate);
            }
        },
        watch: {
            validForm: function (value) {
                if (value) {
                    this.buttonIsDisabled = false
                } else {
                    this.buttonIsDisabled = true
                }
            }
        },
        mounted() {
            this.setDefaultBirthDate();
        }
    }
</script>

